import React, { Component } from 'react'
import NewsItem from './NewsItem'

export class News extends Component {

    articles = []

    constructor() {
        super()
        this.state = {
            articles: this.articles,
            page: 1
            // loading: false
        }
    }


    async componentDidMount() {
        console.log("cmd")
        let url = "https://newsapi.org/v2/top-headlines?country=in&category=business&apiKey=bab43cc2b782486587a36b74ca7b8341&page-2"
        let data = await fetch(url)
        let parsedData = await data.json()
        console.log(parsedData)
        this.setState({ articles: parsedData.articles })
    }

 handleNext = () => {
    console.log('next')
}

 handleprev = () => {
    console.log('priv')

}



render() {
    return (
        <div className="container">
            <div className="text-center my-3 ">

                <h1 >NewsMonkey-Top Headline</h1>
            </div>
            <div className="row">

                {
                    this.state.articles.map
                        (
                            (element) => {
                                return <div className="col-md-4 my-3 " key={element.url}>
                                    <NewsItem title={element.title} discription={element.discription} imageurl={element.urlToImage} newsurl={element.url} />
                                </div>
                            }
                        )
                }
                <div class="d-flex justify-content-around">

                    <button type="button" class="btn btn-secondary my-3" onClick={this.handleprev}>&larr; Primary</button>
                    <button type="button" class="btn btn-secondary my-3" onClick={this.handleNext}>Secondary &rarr;</button></div>

            </div>


        </div>



    )
}
}

export default News
